import React from 'react';

const SearchBox = ({type, nome, searchfield, onsearchChange }) => {
	let messaggio = "";
	if(nome==='Broccola'||nome==='broccola'){
		messaggio = `Ciao Broccola â™¥`;
	}else{
		messaggio =`Ciao ${nome}`;
	}

	let opacity="opacitynullSearch"
	if(type===false)
		opacity="";



	return(

		<input
			id="barracerca"
			className={"zIndexlow tc b--green bg-lightest-blue padding4rem "+ opacity}
			type="search"
			placeholder={messaggio}
			onChange={onsearchChange}

		/>

	);
}

export default SearchBox;



