import React from 'react';
import SearchBox from './SearchBox';


class TopWrapper extends React.Component {
	reloadPage = () => {
		window.location.reload(true)
	}
	mostraCerca= ()=>{
			//alert("mostro titolo")
			if(window.barracerca.classList.contains("opacitynullSearch")){
				window.barracerca.classList.replace("opacitynullSearch","opacitytopSearch")
				window.titoloTop.classList.replace("opacitytopTitolo","opacitynullTitolo")

				window.barracerca.focus()
			}else{
				window.barracerca.classList.replace("opacitytopSearch","opacitynullSearch")
				window.titoloTop.classList.replace("opacitynullTitolo","opacitytopTitolo")

				window.barracerca.blur()
			}
		}
render(){

	let offlineShowed;
	if(this.props.amIOffline===true||(!window.clientInformation.onLine))
		offlineShowed = "sei offline";
		else
		offlineShowed = window.clientInformation.connection.effectiveType;

	let version ="4.3.3"
	let connection = offlineShowed

	let props = this.props
	let titolo ="titolo";
	if(props.props.titoloLoaded)
		titolo = ""
	let opacitytopTitolo = "opacitytopTitolo"
	if(window.barracerca)
		if(window.barracerca.classList.contains("opacitytopSearch"))
			opacitytopTitolo ="opacitynullTitolo"





	if(props.props.check===0&&props.props.type!=='mobile'){console.log("0&&!==mobile")
	return (
		<div  id="topWrapper"  className="Row ">
				<div id="imgCarica" onClick={props.mostraMenu} className="z-1 absolute top-1 left-2">
					<img  src="https://dolfo.altervista.org/verso/images/ilviaggio/menu.png" alt="Menu" height="69" width="69"></img>
				</div>

				<div id="menu" className="absolute widthmenunull menu">
					<div id="imgMenuX"  onClick={props.mostraMenu} className="z-1 absolute top-1 left-2">
						<img  src="https://dolfo.altervista.org/verso/images/ilviaggio/close.png" alt="close" height="69" width="69"></img>
					</div>
					<div id="caricaUl"  onClick={function(){props.mostraFormCaricaImmagine()}} className="caricaUl opacitynullII">
						<h1 className="f3 pointer underline-hover"> Carica <span role="img" aria-label="^">ðŸš€</span> </h1>
					</div>




					<div id="versione" className="versione opacitynullII">
						<h4 onClick={this.reloadPage} style={{margin:"4%"}}>Versione {version}</h4>
						<h4 onClick={this.reloadPage} style={{margin:"4%"}}>Connessione: {connection}</h4>
					</div>


				</div>
				<div onClick={props.mostraMenu} id="menuComplementare" className="absolute menuComplementare bg-black"></div>



	  <div className=" displayInline Column alignCenter ">
	  	<h1 id="titoloTop" className={`z-0 ${opacitytopTitolo} f2 ${titolo}`}> Verso l'infinito... E oltre </h1>
	  </div>

	  <div id="searchBox"  style={{top:"1.5em"}} className=" right-0 left-0 absolute top-2  Column alignCenter">
	  	<SearchBox type={true} nome={props.props.user.nome} onsearchChange={props.searchChange}/>
	  </div>

	  <div id="imgCerca" onClick={this.mostraCerca} className="z-1 absolute top-2 right-2">
		<img src="https://dolfo.altervista.org/verso/images/ilviaggio/search.png" alt="Cerca" height="49" width="49"></img>
	</div>




</div>
											);}else if(props.props.type==='mobile'){if(props.props.check===0){console.log("0&&===mobile")
																						return (
																											<div  id="topWrapper"  className="rowFixed ">
																														<div id="imgCarica"  onClick={props.mostraMenu} className="z-1 absolute top14 left-1">
																															<img  src="https://dolfo.altervista.org/verso/images/ilviaggio/menu.png" alt="Menu" height="41" width="41"></img>
																														</div>
																														<div id="menu" className="absolute widthmenunull menu">
																															<div id="imgMenuX"  onClick={props.mostraMenu} className="z-1 absolute top14 left-1">
																																<img  src="https://dolfo.altervista.org/verso/images/ilviaggio/close.png" alt="close" height="41" width="41"></img>
																															</div>
																															<div id="caricaUl"  onClick={function(){props.mostraFormCaricaImmagine()}} className="caricaUl opacitynullII">
																																<h1 className="f3 pointer underline-hover"> Carica <span role="img" aria-label="^">ðŸš€</span> </h1>
																															</div>





																															<div id="versione" className="versione opacitynullII">
																																<h4 onClick={this.reloadPage} style={{margin:"4%"}}>Versione {version}</h4>
																																<h4 onClick={this.reloadPage} style={{margin:"4%"}}>Connessione: {connection}</h4>
																															</div>

																														</div>
																														<div onTouchStart={props.mostraMenu} id="menuComplementare" className="absolute menuComplementare bg-black"></div>


																													    <div className=" displayInline Column alignCenter ">
																													    	<h2 id="titoloTop" className={`z-0 ${opacitytopTitolo} ${titolo}`}>  Verso l'infinito... E oltre </h2>
																													    </div>

																													    <div  style={{top:"1em"}} id="searchBox" className=" right-0 left-0 absolute top-2  Column alignCenter">
																													    	<SearchBox type={true} nome={props.props.user.nome} onsearchChange={props.searchChange}/>
																													    </div>

																													    <div id="imgCerca"  onClick={this.mostraCerca} className="z-1 top21 absolute right-1">

																															<img src="https://dolfo.altervista.org/verso/images/ilviaggio/search.png" alt="Cerca" height="27" width="27"></img>
																														</div>




																													</div>
																										);}else{console.log("1&&===mobile")

																							return (
																								<div  id="topWrapper"  className="rowFixed ">
																											<div id="imgCarica"  onClick={props.mostraMenu} className="z-1 absolute top14 left-1">
																												<img  src="https://dolfo.altervista.org/verso/images/ilviaggio/menu.png" alt="Menu" height="41" width="41"></img>
																											</div>
																											<div id="menu" className="absolute widthmenunull menu">
																												<div id="imgMenuX"  onClick={props.mostraMenu} className="z-1 absolute top14 left-1">
																													<img  src="https://dolfo.altervista.org/verso/images/ilviaggio/close.png" alt="close" height="41" width="41"></img>
																												</div>
																												<div id="caricaUl"  onClick={function(){props.mostraFormCaricaImmagine()}} className="caricaUl opacitynullII">
																													<h1 className="f3 pointer underline-hover"> Carica <span role="img" aria-label="^">ðŸš€</span> </h1>
																												</div>




																												<div id="versione" className="versione opacitynullII">
																													<h4 onClick={this.reloadPage} style={{margin:"4%"}}>Versione {version}</h4>
																													<h4 onClick={this.reloadPage} style={{margin:"4%"}}>Connessione: {connection}</h4>
																												</div>

																											</div>
																											<div onTouchStart={props.mostraMenu} id="menuComplementare" className="absolute menuComplementare bg-black"></div>



																													    <div className=" displayInline Column alignCenter ">
																													    	<h2 id="titoloTop" className={`z-0 ${opacitytopTitolo} ${titolo}`}>  Verso l'infinito... E oltre </h2>
																													    </div>

																													    <div  style={{top:"1em"}} id="searchBox" className="  right-0 left-0 absolute   Column alignCenter">
																													    	<SearchBox type={true} nome={props.props.user.nome} onsearchChange={props.searchChange}/>
																													    </div>

																													    <div id="imgCerca"  onClick={this.mostraCerca} className="z-1 top21 absolute right-2">

																															<img src="https://dolfo.altervista.org/verso/images/ilviaggio/search.png" alt="Cerca" height="27" width="27"></img>
																														</div>




																													</div>
																										);

																						}




																					} else {
														if(window.innerWidth>= 1057){console.log("0&&!==mobile>=1057")
								return (
													<div  id="topWrapper"  className="Row ">


														<div className="Column alignCenter">
																<h1 className="f3 pointer underline-hover displayInline" onClick={function(){props.mostraFormCaricaImmagine()}}> Carica <span role="img" aria-label="^">ðŸš€</span></h1>
														</div>



													    <div className="Column alignCenter">
													    	<h1 id="titoloTop" className={`f2 ${titolo}`}> Verso l'infinito... E oltre </h1>
													    </div>

													    <div className="Column alignCenter">
													    	<SearchBox type={false} nome={props.props.user.nome} onsearchChange={props.searchChange}/>
													    </div>




													</div>
												);}else{console.log("1&&!==mobile<=1057")

									return (


														<div  id="topWrapper"  className="Row ">
																	<div id="imgCarica"  onClick={props.mostraMenu} className="z-1 absolute top-1 left-2">
																		<img  src="https://dolfo.altervista.org/verso/images/ilviaggio/menu.png" alt="Menu" height="69" width="69"></img>
																	</div>
																	<div id="menu" className="absolute widthmenunull menu">
																		<div id="imgMenuX"  onClick={props.mostraMenu} className="z-1 absolute top-1 left-2">
																			<img  src="https://dolfo.altervista.org/verso/images/ilviaggio/close.png" alt="close" height="69" width="69"></img>
																		</div>
																		<div id="caricaUl"  onClick={function(){props.mostraFormCaricaImmagine()}} className="caricaUl opacitynullII">
																			<h1 className="f3 pointer underline-hover"> Carica <span role="img" aria-label="^">ðŸš€</span> </h1>
																		</div>




																		<div id="versione" className="versione opacitynullII">
																			<h4 className="pointer" onClick={this.reloadPage} style={{margin:"4%"}}>Versione {version}</h4>
																			<h4 onClick={this.reloadPage} style={{margin:"4%"}}>Connessione: {connection}</h4>
																		</div>

																	</div>
																	<div onClick={props.mostraMenu} id="menuComplementare" className="absolute menuComplementare bg-black"></div>



													    <div className=" displayInline Column alignCenter ">
													    	<h1 id="titoloTop" className={`z-0 ${opacitytopTitolo} f2 ${titolo}`}> Verso l'infinito... E oltre </h1>
													    </div>

													    <div id="searchBox" style={{top:"1.5em"}} className="  right-0 left-0 absolute top-2 Column alignCenter">
													    	<SearchBox type={true}nome={props.props.user.nome} onsearchChange={props.searchChange}/>
													    </div>

													    <div id="imgCerca" onClick={this.mostraCerca} className="z-1 absolute top-2 right-2">
															<img src="https://dolfo.altervista.org/verso/images/ilviaggio/search.png" alt="Cerca" height="49" width="49"></img>
														</div>




													</div>
												);

								}
						}
					}
};

export default TopWrapper;



